import React from 'react';

import api from '../lib/api';
import Headline from '../components/headline/headline';
import Paragraph from '../components/paragraph/paragraph';
import Frame from '../components/frame/frame';

type Props = {
  items: Array<{ id: number; headline: string; conten: string }>;
  anschrift: string;
  pageTitle: string;
  pageDescription: string;
};

export default function Blog(data: Props) {
  console.log(data);
  return (
    <Frame singlePage title={data.pageTitle} description={data.pageDescription}>
      <Headline>Blog</Headline>

      {data.items.map((i) => (
        <Headline level={3} key={i.id}>
          {' '}
          {i.headline}
        </Headline>
      ))}
    </Frame>
  );
}

Blog.getInitialProps = () => api('Blog');
